import { Component } from '@angular/core';
import { NgForm } from '@angular/forms';
import { UserService } from '../../servises/user.service'
import { Router } from '@angular/router';
import { User } from '../../models/user.model'
@Component({
  selector: 'sn-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent {
  username: string;
  password: string;
  user: boolean = false;
  constructor(private router: Router, private userService: UserService) { }
  //not fixed function

  ngOnInit() {
    if (localStorage.getItem('token') != null)
      this.user = true;
  }
  onCreateUser(form: NgForm) {

  }
  ngLogin(username, password) {
    const loginUser: User = {
      email: 'nevermind',
      username: username.value,
      password: password.value,
      passwordConf: 'nevermind',
      city: 'nevermind'
    };
    this.userService.LogIn(loginUser);
    //setTimeout(() => {
    //  if (localStorage.getItem('token') != null) {
    //    this.userService.checkRole(localStorage.getItem('token'));
    //  }
    //  this.router.navigate(['/home-page']);
    //}, 300);
  }

  onLogin() {
    this.router.navigate(['/login']);
  }
  onSignIn() {
    this.router.navigate(['/sign']);
  }

  onHome() {
    this.router.navigate(['/home-page']);
  }
  onPie() {
    this.router.navigate(['/app-pie']);
  }
  onProducts() {
    this.router.navigate(['/app-our-products']);
  }
  onUsers() {
    this.router.navigate(['/app-our-users']);
  }
  onShoppingList() {
    this.router.navigate(['/app-shopping-cart']);
  }
  onSpenderPie() {
    this.router.navigate(['/app-big-spenders']);
  }
  onOrders() {
    this.router.navigate(['/app-orders']);
  }
}
