import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '../servises/user.service';
import { ProductService } from '../servises/product.service';
import { Product } from '../models/product.model';

@Component({
  selector: 'app-product-create',
  templateUrl: './product-create.component.html',
  styleUrls: ['./product-create.component.css']
})
export class ProductCreateComponent implements OnInit {
  user: boolean = false;
  token: string;
  constructor(private router: Router, private userService: UserService, private ProductService: ProductService) { }

  ngOnInit() {
    if (localStorage.getItem('token') != null)
      this.user = true;
  }
  onLogin() {
    this.router.navigate(['/login']);
  }
  onSignIn() {
    this.router.navigate(['/sign']);
  }

  onHome() {
    this.router.navigate(['/home-page']);
  }
  onPie() {
    this.router.navigate(['/app-pie']);
  }
  onProducts() {
    this.router.navigate(['/app-our-products']);
  }
  onUsers() {
    this.router.navigate(['/app-our-users']);
  }
  onShoppingList() {
    this.router.navigate(['/app-shopping-cart']);
  }
  onSpenderPie() {
    this.router.navigate(['/app-big-spenders']);
  }
  onOrders() {
    this.router.navigate(['/app-orders']);
  }
  onCreate(name, price, quantity, category) {
    this.token = localStorage.getItem('token');
    this.ProductService.createProduct(this.token, name.value, price.value, quantity.value, category.value);
    this.router.navigate(['/app-our-products']);
  }
}
